import React, { useState } from 'react';
import Card from './Card';
import español from './español';
import english from './english';

// const español = ['cantar', 'hablar', 'haber', 'vivir', 'adivinar', 'correr', 'andar'];
// const english = ['to sing', 'to speak', 'to have', 'to live', 'to guess', 'to run', 'to walk'];

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

function App() {

  let [next, setNext] = useState(0);
  let [reverse, toggleReverse] = useState(false);

  let questions = español;
  let answers = english;

  if (reverse) {
    questions = english;
    answers = español;
  }

  return <div className="App">
    <Card
      key={questions[next]}
      question={questions[next]}
      answer={answers[next]}
      onNext={() => setNext(getRandomInt(questions.length - 1))}
      onBack={() => setNext(next === 0 ? 0 : next - 1)}
      onReverse={() => toggleReverse(!reverse)}
    />
  </div>
}

export default App;
